.zebulon-table-grid: {
  border: solid lightgrey 0.5em;
  /*border-left: unset;*/
}
.zebulon-table-title: {
  font-weight: bold;
  text-align: center;
  padding: 4px;
  border: solid 0.05em;
}
.zebulon-table-input {
  font: inherit;
  height: inherit;
  width: inherit;
  border: solid rgba(0, 0, 0, 0.2) 0.02em;
