  /*border: solid lightgrey thin;
  box-sizing: border-box;
  padding: 0;
  background-color: inherit;*/
  height: 25px;
}
.zebulon-input-select: {
  width: inherit;
}
.zebulon-table-cell {
  overflow: hidden;
  white-space: nowrap;
  height: inherit;
  width: inherit;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: solid rgba(0, 0, 0, 0.3) 0.03em;
  border-right: unset;
  border-top: unset;
  padding: 0.2em;
  outline: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.zebulon-table-cell-odd {
  background-color: rgba(0, 0, 0, 0.1);
}
.zebulon-table-cell-audit {
  border-top: solid blue 0.02em;
  border-bottom: solid blue 0.02em;
}
.zebulon-table-cell-selected {
  background-color: lightcyan;
}
.zebulon-table-cell-focused {
  border: solid rgba(0, 0, 0, 0.7) 0.04em;
}
.zebulon-table-cell-editable {
  font: inherit;
}
.zebulon-table-select {
  font: inherit;
  height: inherit;
  width: inherit;
}
.zebulon-grid-select {
  font: inherit;
  height: inherit;
  width: inherit;
}
.zebulon-table-header {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font: inherit;
  /*outline: none;*/
  background-color: #eef8fb;
  /* overflow: hidden;
  white-space: nowrap;
  height: inherit;
  box-sizing: border-box;*/
  text-align: center;
  /*border: solid rgba(0, 0, 0, 0.2) 0.02em;*/
  /*border: 0;*/
  padding: 0.2em;
}
.zebulon-table-filter {
  padding: 0;
  /*margin: 0.01em;*/
  /*border: solid rgba(0, 0, 0, 0.2) 0.05em;*/
}
.zebulon-table-filter-empty {
  background-color: white;
}
.zebulon-table-filter-checkbox {
  padding: 0.2em;
}
.zebulon-table-filter-checkbox-undefined {
  background-color: lightgrey;
}
.zebulon-table-status {
  position: absolute;
  outline: none;
  background-color: rgba(0, 0, 0, 0.2);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  /*border: solid rgba(0, 0, 0, 0.3) 0.02em;*/
  border: solid rgba(0, 0, 0, 0.4) 0.04em;
  border-top: unset;
  border-right: unset;
  padding: 0.2em;
  overflow: hidden;
}
.zebulon-table-status-selected {
  border: solid grey 0.04em;
  background-color: #eef8fb;
}
.zebulon-table-corner {
  /*position: relative;*/
  outline: none;
  background-color: #eef8fb;
  height: inherit;
  border: solid rgba(0, 0, 0, 0.4) 0.03em;
  border-top: unset;
  border-right: unset;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /*z-index: 1;*/
  overflow: hidden;
}
