  width: fit-content;
  min-width: 200px;
  min-height: 100px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin: 0 auto;
  padding: 20px;
  z-index: 1000;
  opacity: 1;
